@font-face {
  font-family: 'pretendard';
  font-style: normal;
  src: url('./font/Pretendard-Light.otf') format('opentype');
}

@font-face {
  font-family: 'pretendard-bold';
  font-style: normal;
  src: url('./font/Pretendard-Bold.otf') format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  src: url('./font/ArchivoNarrow-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Hahmlet', serif;
  font-style: normal;
  src: url('./font/Hahmlet-Medium.ttf') format('truetype');
}

.App {
  position: relative;
  width: 1440px;
  height: 1024px;
  background: #F5F5F5;
}

.jps {
  position: absolute;
  width: 84px;
  height: 54px;
  left: 30px;
  top: 18px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  font-size: 45px;
  line-height: 54px;
  text-align: center;
  letter-spacing: -0.03em;

  color: #5590C9;
}

.login-button {
  position: absolute;
  width: 228px;
  height: 47px;
  left: 1182px;
  top: 22px;

  background: #5590C9;
  border-radius: 23.5px;
}

.login-button > a {
  position: absolute;
  width: 143px;
  height: 24px;
  left: 43px;
  top: 12px;

  font-family: 'Pretendard';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height */

  text-align: center;
  letter-spacing: -0.03em;

  color: #F5F5F5;
}

